import React, { useEffect , useState } from 'react';
import './App.css';
import Tmdb from './Tmdb';
import Linhadefilme from './components/LinhaDeFilme';
import FilmeDestaque from './components/FilmeDestaque';
import Cabecalho from './components/Cabecalho/';

// eslint-disable-next-line
export default () => {

  /* Defini√ß√£o da v√°riavel de lista de filme, que ser√° utilizada para exibir todas as listas de filme na p√°gina principal */
  const [listaFilme, setlistaFilme] = useState ([]);
  /* Defini√ß√£o da v√°riavel de dados do filme destaque, que ser√° exibido tamb√©m na p√°gina inicial mas separadamente dos outros filmes, como um destaque */
  const [dadosDestaque, setDadosDestaque] = useState(null);
  /* Defini√ß√£o da v√°riavel da cor do fundo do cabe√ßalho (header), utilizada no momento da rolagem pela p√°gina, com a fun√ß√£o de aparecer e desaparecer */
  const [fundoCabecalho, setFundoCabecalho] = useState(false);

  useEffect(()=>{
    const carregarTudo = async () => {
      // Pegando a lista total dos filmes da p√°gina principal
      let list = await Tmdb.getHomeList();
      setlistaFilme(list);

      // Pegando o Destaque
      let originais = list.filter(i=>i.slug === 'originais');
      let escolhaAleatoria = Math.floor(Math.random() * (originais[0].items.results.length - 1));
      let escolhido = originais[0].items.results[escolhaAleatoria]
      let infoEscolhido = await Tmdb.getInfoFilme(escolhido.id, 'tv');
      setDadosDestaque(infoEscolhido);
      /* Nesta parte temos uma fun√ß√£o matem√°tica para escolher aleat√≥riamente um filme da lista de originais da TECFLIX (NETFLIX) e assim mostr√°-lo no destaque da p√°gina */
    }

    carregarTudo();
  }, []);
  
  /* O uso do hook "useEffect" nesta situa√ß√£o serve para perceber um a√ß√£o dentro da p√°gina, que no caso √© a a√ß√£o de rolagem da p√°gina.
     Assim sua funcionalidade √© que quando ele percebe esta rolagem da p√°gina, ativa-se ou desativa-se o "setFundoCabecalho" que far√° com que a parte de
     cima da p√°gina (o header) fique preto ou transparente, e assim n√£o interrompa a busca dos filmes pela lista */
  useEffect(()=>{
    const scrollCheck = () => {
      if(window.scrollY > 10) {
        setFundoCabecalho(true);
      } else {
        setFundoCabecalho(false);
      }
    }
    window.addEventListener('scroll', scrollCheck);
    return () => {
    window.removeEventListener('scroll', scrollCheck);
    }
  }, []);

  
/* Montagem da p√°gina, disposi√ß√£o dos elementos, cabe√ßalho, o filme destaque, as listas dos filmes, o 
   rodap√© da p√°gina e a fun√ß√£o de carregar (loading) antes de abrir a p√°gina */
  return (
    <div className="page">

      <Cabecalho fundo={fundoCabecalho}/>

      {dadosDestaque &&
        <FilmeDestaque item={dadosDestaque} />
      }

      <section className="listas">
        {listaFilme.map((item, key)=>(
          <Linhadefilme key={key} title={item.titulo} items={item.items} />
        ))}
      </section>

      <footer>
        Feito com <span role="img" aria-label="cora√ß√£o">üíñ</span> por Jean e Nicolas<br/>
        Direitos de imagem para Netflix<br/>
        API utilizada do site Themoviedb.org
      </footer>

      {listaFilme.length <= 0 &&
      <div className="carregando">
        <img src="https://motamachado.com.br/wp-content/themes/motamachado/assets/images/carregando.gif" alt="Carregando"/>
      </div>
      }
    </div>
  );
}